import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'

import NotFound from '../components/global/NotFound'
import { createCategory, deleteCategory, updateCategory } from '../redux/actions/categoryAction'
import { FormSubmit, ICategory, RootStore } from '../utils/TypeScript'

const Category = () => {
  const [name, setName] = useState('')
  const [edit, setEdit] = useState<ICategory | null>(null)
  const {auth, categories} = useSelector((state: RootStore) => state)
  const dispatch = useDispatch()

  const handleSubmit = (e: FormSubmit) => {
      e.preventDefault()
    if(!auth.access_token || !name) return
    if(edit) {
        if(edit.name === name) return
        const data = {...edit, name}
        dispatch(updateCategory(data, auth.access_token))
    } else {
        dispatch(createCategory(name, auth.access_token))
        setName('')
        setEdit(null)
    }
  }
  
  const handleDelete = (id: string) => {
      if(!auth.access_token) return
      if(window.confirm('Ban co chac chan muon xoa?')) {
        dispatch(deleteCategory(id, auth.access_token))
      }
  }

  useEffect(() => {
      if(edit) setName(edit.name)
  }, [edit])

  if(auth.user?.role !== 'admin') return <NotFound />
  return (
    <div className="category">
      <form onSubmit={handleSubmit}>
        <label htmlFor="category">Category</label>

        <div className="d-flex">
            {
                edit && <span onClick={() => setEdit(null)}>X</span>
            }
          <input type="text" name="category" id="category"
          value={name} onChange={e => setName(e.target.value)} />

          <button type="submit">{edit ? 'Update' : 'Create'}</button>
        </div>
      </form>

      <div>
        {
          categories.map(category => (
            <div className="category_row" key={category._id}>
              <p className="m-0 text-capitalize">{category.name}</p>

              <div>
                <span onClick={() => setEdit(category)}>
                    <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" aria-hidden="true" role="img" width="1em" height="1em" preserveAspectRatio="xMidYMid meet" viewBox="0 0 1024 1024"><path d="M880 836H144c-17.7 0-32 14.3-32 32v36c0 4.4 3.6 8 8 8h784c4.4 0 8-3.6 8-8v-36c0-17.7-14.3-32-32-32zm-622.3-84c2 0 4-.2 6-.5L431.9 722c2-.4 3.9-1.3 5.3-2.8l423.9-423.9a9.96 9.96 0 0 0 0-14.1L694.9 114.9c-1.9-1.9-4.4-2.9-7.1-2.9s-5.2 1-7.1 2.9L256.8 538.8c-1.5 1.5-2.4 3.3-2.8 5.3l-29.5 168.2a33.5 33.5 0 0 0 9.4 29.8c6.6 6.4 14.9 9.9 23.8 9.9z" fill="currentColor"/></svg>
                </span>
                <span onClick={() => handleDelete(category._id)}>
                    <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" aria-hidden="true" role="img" width="1em" height="1em" preserveAspectRatio="xMidYMid meet" viewBox="0 0 1024 1024"><path d="M292.7 840h438.6l24.2-512h-487z" fill-opacity=".15" fill="currentColor"/><path d="M864 256H736v-80c0-35.3-28.7-64-64-64H352c-35.3 0-64 28.7-64 64v80H160c-17.7 0-32 14.3-32 32v32c0 4.4 3.6 8 8 8h60.4l24.7 523c1.6 34.1 29.8 61 63.9 61h454c34.2 0 62.3-26.8 63.9-61l24.7-523H888c4.4 0 8-3.6 8-8v-32c0-17.7-14.3-32-32-32zm-504-72h304v72H360v-72zm371.3 656H292.7l-24.2-512h487l-24.2 512z" fill="currentColor"/></svg>              
                </span>
                </div>
            </div>
          ))
        }
      </div>
    </div>
  )
}

export default Category